<template>
  <div
    @click="fire"
    :class="`${필터}  filter-item`"
    :style="`background-image:url(${이미지})`"
  >
    <slot></slot>
    <!-- slot props 1. <slot :자식데이터="자식데이터"></slot> 
    2.부모는 <template v-slot="작명">후에 {{작명.자식데이터}} -->
    <!-- <slot name="a"></slot>
    <slot name="b"></slot> -->
    <!-- 사진위에 필터명을 표기해주자 slot으로 부모 > 자식 데이터전송가능 -->
    <!-- slot으로 부모 > 자식 데이터전송법 1. 자식은 구멍뚫기
    태그안에 (html안에 내용)데이터바인딩 할때만  slot가능
    slot여러개 사용하는법 1. <slot name="a"></slot>
    2.<template v-slot:a>보낼거</template>
    근데 여러개를 데이터바인딩하고플땐 그냥 props쓰는게 낫다
    slot의 장점은 간단한 전송법과 그냥 html태그 까지 전송할수 있는게 장점!
     -->
  </div>
</template>

<script>
export default {
  name: "fitlerbox",
  data() {
    return {
      msg: "hello",
    };
  },
  props: {
    이미지: String,
    필터: String,
  },
  methods: {
    fire() {
      //mitt로 데이터전송법
      // 1.this.emitter.emit()으로 발사하고
      // 2.this.emitter.on()으로 수신하면 됨

      //필터박스 클릭시 app.vue로 필터명전송
      //this.emitter.emit("작명", 데이터);
      this.emitter.emit("boxClick", this.필터);
    },
  },
};
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
  background-position: center;
}
</style>
