<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>
  <Container
    @write="작성한글 = $event"
    :이미지="이미지"
    :게시물="게시물"
    :step="step"
  />
  <button type="button" @click="more">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        multiple
        type="file"
        id="file"
        class="inputfile"
      />
      <!-- multiple은 ㅇ파일 여러개 선택가능 -->
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

  <!-- <div v-if="step == 0">내용 0</div>
  <div v-if="step == 1">내용 1</div>
  <div v-if="step == 2">내용 2</div>
  <button @click="tabIndex(0)">버튼 0</button>
  <button @click="tabIndex(1)">버튼 1</button>
  <button @click="tabIndex(2)">버튼 2</button> -->
</template>

<script>
import Container from "./components/Container.vue";
import Postdata from "./assets/postdata.js";
import axios from "axios";
axios.post();

export default {
  name: "App",
  data() {
    return {
      게시물: Postdata,
      더보기: 0,
      step: 0,
      이미지: "",
      작성한글: "",
      선택한필터: "",
    };
  },
  components: {
    Container,
  },

  methods: {
    more() {
      //axios 다른 문법 post POST 요청을 보낼 수도 있습니다.
      //POST 요청은 서버로 원하는 데이터를 전송할 수 있습니다. 문자, object 다 가능합니다.

      // axios
      //   .post("URL", { name: "Kim" }) {name:kim}은 보낼데이터임
      //   .then()
      //   .catch((err) => {
      //     err;
      //   });

      axios
        .get(`https://codingapple1.github.io/vue/more${this.더보기}.json`) //서버로 get요청해서 데이터 가져옴
        .then((result) => {
          console.log(result.data); //.data하면 게시물 데이터만 가져ㅕ옴
          this.게시물.push(result.data); //어레이에 데이타 추가 문법 :push
          this.더보기++;
        });
    },
    //tab 함수
    // tabIndex(idx) {
    //   this.step = idx;
    // },
    upload(e) {
      let file = e.target.files; //내가업로드한 파일이 담겨있음
      console.log(file[0]); //file[0].type 은 이미지가 어떤형태인지 나옴
      //업로드 후엔 담페이지로 보내야함
      //업로드한 이미지 띄우기
      //업로드한 이미지의 url을 생성해주려면
      let url = URL.createObjectURL(file[0]);
      this.이미지 = url;
      this.step++;
    },
    publish() {
      var 내게시물 = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.이미지,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.작성한글,
        filter: this.선택한필터,
      };
      this.게시물.unshift(내게시물); //왼쪽의 어레이에 자료 집어 넣어줌 맨앞으로
      this.step = 0;
    },
  },
  mounted() {
    this.emitter.on("boxClick", (a) => {
      this.선택한필터 = a;
    });
  },
};
</script>

